































import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import XComponentTemplate from '@/components/component-template.vue';

import XPanel from '@/components/panel.vue';
import * as code from './button';

const card = [
  {
    col: '12',
    title: 'Button',
    dec: '',
    code: code.button_base
  },
  {
    col: '12',
    title: 'Ghost Button',
    dec: '',
    code: code.button_ghost
  },
  {
    col: '12',
    title: 'Link Button',
    dec: '',
    code: code.button_link
  },
  {
    col: '12',
    title: 'Button Size',
    dec: 'Codeages Design ',
    code: code.button_size
  },
  {
    col: '12',
    title: 'Disabled Button',
    dec: '',
    code: code.button_disabled
  }
];
 
const api = {
  code: code.button_api,
  eventData: [
    {
      name: 'loading',
      dec: 'loading',
      callback: '--',
      args: 'args1: $elDomjquery'
    },
    {
      name: 'reset',
      dec: 'loading',
      callback: '--',
      args: 'args1: $elDomjquery'
    }
  ],
  attrData: [
    {
      name: 'el',
      dec: 'Dom',
      type: 'String',
      value: '',
      optional: '--'
    },
    {
      name: 'loadingText',
      dec: 'loading',
      type: 'String',
      value: 'loading...',
      optional: '--',
      data: 'loading-text'
    }
  ]
}

@Component({
  components: {
    XComponentTemplate,
    XPanel
  },
})
export default class extends Vue {
  card: any = card;
  api: any = api;
  code: any = code;
  
  loading(event) {
    const btn = cd.btn({
      el: event.currentTarget,
      loadingText: '...'
    });

    btn.trigger('loading', () => {
      console.log('loading...');
    });

    setTimeout(() => {
      btn.trigger('reset', () => {
        console.log('reset...');
      });
    }, 1000);
  }
}



