









import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import XComponentTemplate from '@/components/component-template.vue';

import * as code from './autocomplete';

const card = [
  {
    col: '12',
    title: '',
    code: code.autoComplete_base
  }
]

const api = {
  code: code.autocomplete_api,
  attrData: [
    {
      name: 'el',
      dec: 'Dom',
      type: 'String',
      value: '',
      optional: '--'
    },
    {
      name: 'sources',
      dec: ', FunctionPromise, resolve',
      type: 'Array | Function',
      value: '',
      optional: '--'
    },
    {
      name: 'selectKey',
      dec: 'selectKeykey',
      type: 'String',
      value: '',
      optional: '--'
    },
  ],
  hookData: [
    {
      name: 'change',
      dec: '',
      callback: '--',
      args: 'args1: value'
    }
  ]
}

@Component({
  components: {
    XComponentTemplate,
  }
})
export default class extends Vue {
  card: any = card;
  api: any = api;

  mounted() {
    cd.autocomplete({
      el: '#cd-autocomplete',
      sources: [
        {
          id: '1',
          name: '11',
        },
        {
          id: '2',
          name: '12',
        },
        {
          id: '3',
          name: '13',
        },
        {
          id: '4',
          name: '21'
        }
      ],
      selectKey: 'name'
    });
  }
}



