










import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import XComponentTemplate from '@/components/component-template.vue';

import * as code from './alert';

const card = [
  {
    col: '12',
    title: '',
    dec: '',
    code: code.alert_color,
  },
  {
    col: '12',
    title: '',
    dec: '',
    code: code.alert_close
  }
];

const api = {
  code: code.alert_api,
  attrData: [
    {
      name: 'el',
      dec: 'Dom',
      type: 'String',
      value: '',
      optional: '--'
    }
  ],
  hookData: [
    {
      name: 'close',
      dec: '',
      callback: '--',
      args: 'args1: $alertDomjquery'
    }
  ]
}

@Component({
  components: {
    XComponentTemplate
  },
})
export default class extends Vue {
  card: any = card;
  api: any = api;
 
  mounted() {
    cd.alert({
      el: '.js-cd-alert',
    }).on('close', () => {
      console.log('');
    })
  }
}



